<template>
    <el-upload 
        :action="option.action" 
        :accept="option.accept" 
        :show-file-list="option.showFileList"
        :list-type="option.listType"
        :auto-upload="option.autoUpload"
        :limit="option.limit" 
        :file-list="fileList"
        :multiple="option.multiple"
        :on-preview="handlePreview" 
        :on-remove="handleRemove" 
        :before-remove="beforeRemove"
        :on-success="handleSuccess" 
        :on-exceed="handleExceed" 
        ref="refUpload">
        <el-button size="small" type="primary">点击上传</el-button>
        <div slot="tip" class="el-upload__tip">{{option.noteMessage}}</div>
    </el-upload>
</template>
<script>
/**
 * params: config 配置信息
 * params: fileList [Array: Object]
 *     [{name: 'filename',url: 'file-url.jpg', status: 'success'}]
 */
import config from '@/config'
let imgPrefix = config.imgHost
const defaultOption = {
    action: config.uploadAction,
    // limit: 1,
    accept: '*',
    showFileList: true,
    multiple: false,
    listType: 'text',
    autoUpload: true,
    noteMessage: '只能上传jpg/png文件，且不超过500kb',
}

export default {
    name: 'uploadImage',
    props: {
        config: {
            default (){
                return {}
            }
        },
       fileList: {
            type: [Array],
            default () {
                return []
            }
        }
        
    },
    data() {
        return {
        };
    },
    watch: {
        fileList: {
            handler (n,o){
                // 强制清空uploadFiles 复用组件
                if (this.$refs.refUpload) {
                    this.$refs.refUpload.clearFiles()
                }
            },
            deep: true
        }
    },
    computed: {
        option (){
            let _o = Object.assign({},defaultOption,this.config)
            // if (!_o.multiple) {_o.limit = 1}
            return _o
        },
    },
    methods: {
        handleRemove(file, fileList) {
            this.$emit('on-remove',file,fileList,this)
        },
        handlePreview(file) {
            this.$emit('on-preview',file,this)
        },
        handleExceed(files, fileList) {
            this.$message.warning(`当前限制选择${this.option.limit}个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
        },
        beforeRemove(file, fileList) {
            return this.$confirm(`确定移除 ${ file.name }？`);
        },
        handleSuccess(response, file, fileList){
            this.$emit('on-success',response, file, fileList,this)
        }
    },

}
</script>


