export default {
    data() {
        return {
            provinceList: [],
            cityList: []
        }
    },
    methods: {
        handlerProvinceChange(orgCode) {
            this.getCityList(orgCode)
        },
        getProvinceList() {
            this.$api.common.getProvinceList().then(data => {
                this.provinceList = data

            })
        },
        getCityList(org_code) {
            this.$api.common.getCityList({ org_code }).then(data => {
                this.cityList = data
                console.log(this.provinceList, this.cityList)
            })
        }
    },
    created() {
        this.getProvinceList()

    }
}


